<template>
	<div id="store-home">
		<public-header :title="title"></public-header>
		<public-footer :preLoading="showPreLoading" :menufooter="menufooter" v-if="gconfig && gconfig.mall.version == 2"></public-footer>
		<div class="container" >
			<diy
				:diydata="diydata"
				:active = 'active'
				:is_grant = 'is_grant'
				:preLoading="showPreLoading"
				@onChangeTab = "onChangeTab"
				@getCoupon = "getCoupon"
			>
			</diy>
		</div>
		<!--快捷导航-->
		<template v-if="store.menu && store.menu.path && store.menu.path.home == 1">
			<navigator :menu="menu"></navigator>
		</template>
	</div>
</template>

<script>
	import PublicHeader from '@/components/header'
	import PublicFooter from '@/components/footer'
	import diy from '@/components/diy'
	import {mapState, mapMutations} from 'vuex'
	import navigator from '@/components/navigator'
	export default {
		data() {
			return {
				showPreLoading: true,
				is_grant: 0,
				title: '',
				active: 1,
				diydata: {
					diy: {
						data: {}
					}
				},
				gconfig: {
					mall: {}
				},
				store: {},
				menufooter: {},
				menu: {
					css: {},
					params: {},
					data: {},
					position: {
						left: 'inherit',
						bottom: '80px',
						right: '15px'
					},
				},
				storeCouponTop: 0,
				storeOnsaleTop: 0,
				storeEvaluateTop: 0,
			}
		},
		components: {
			PublicHeader,
			PublicFooter,
			diy,
			navigator
		},
		methods: {
			...mapMutations([
				'setLocation', 'getLocation'
			]),

			onLoad() {
				this.util.request({
					url: 'wmall/store/home/index',
					data: {
						sid: this.sid || 3,
						gconfig: 1,
						menufooter: 1
					},
				}).then((res) => {
					this.showPreLoading = false;
					this.gconfig = window.gconfig;
					let result = res.data.message;
					if(result.errno) {
						this.$toast(result.message);
						return;
					}
					this.diydata.diy.data = result.message.homepage;
					this.title = this.diydata.diy.data.page.title || '门店首页';
					this.util.setWXTitle(this.title);
					this.menufooter = window.menufooter;
					//快捷导航
					this.store = result.message.store;
					if(this.store.menu && this.store.menu.data) {
						this.menu = this.util.extend(this.menu, this.store.menu.data);
					}
					this.$nextTick(() => {
						this.onCalculateStoreNavHeight();
					})
				})
			},
			onChangeTab(index) {
				var that = this;
				if(index == 0) {
					var scrollTop = that.storeCouponTop - 46;
				} else if(index == 1) {
					var scrollTop = that.storeOnsaleTop - 46;
				} else if(index == 2) {
					var scrollTop = that.storeEvaluateTop - 46;
				}
				window.scrollTo(0, scrollTop)
			},
			getCoupon() {
				this.util.request({
					url: 'wmall/channel/coupon/get',
					data: {
						sid: this.sid
					},
				}).then((res) => {
					let result = res.data.message;
					if(result.errno == 0) {
						this.util.$toast(result.message, '', 1000);
						this.is_grant = 1;
					} else {
						this.util.$toast('领取失败', '', 1000);
					}
				})
			},

			// 计算商户详情页标签相关高度
			onCalculateStoreNavHeight() {
				var couponDom = document.getElementsByClassName('diy-store-coupon');
				if(couponDom && couponDom.length > 0) {
					this.storeCouponTop = document.getElementsByClassName('diy-store-coupon')[0].offsetTop;
				}
				var onsaleDom = document.getElementsByClassName('diy-store-onsale');
				if(onsaleDom && onsaleDom.length > 0) {
					this.storeOnsaleTop = document.getElementsByClassName('diy-store-onsale')[0].offsetTop;
				}
				var evaluateDom = document.getElementsByClassName('diy-store-evaluate');
				if(evaluateDom && evaluateDom.length > 0) {
					this.storeEvaluateTop = document.getElementsByClassName('diy-store-evaluate')[0].offsetTop;
				}
			},
		},
		created() {
			this.query = this.$route.query;
			if(this.query) {
				this.sid = this.query.sid;
			}
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>

</style>


